import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { NgModule, ViewChild } from '@angular/core';
import { NgbModule, NgbModal, NgbTypeaheadSelectItemEvent } from '@ng-bootstrap/ng-bootstrap';
import { ElementRef } from "@angular/core";
import { NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';
const my = new Date();
import { Subject, Subscription, Observable, merge } from 'rxjs';
import { DataTableDirective } from "angular-datatables";
import { ReportService } from 'src/app/dataService/report-service';
import { map, startWith } from 'rxjs/operators';
import { debounceTime } from "rxjs/internal/operators/debounceTime";
import { distinctUntilChanged } from "rxjs/internal/operators/distinctUntilChanged";
import { filter } from "rxjs/internal/operators/filter";
import swal from 'sweetalert';
import { Router } from '@angular/router';
import { MasterReadService } from "src/app/dataService/masterread-service";
import { MemoReport } from 'src/app/dataService/memo-report';
import *  as moment from 'moment';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { LRDto } from 'src/app/dto/LR-dto';
import { RateMasterDto } from 'src/app/dto/RateMaster-dto';
import { AgentDetailsDto } from 'src/app/dto/AgentDetails-dto';

@Component({
    selector: 'app-performance-report',
    templateUrl: './performance-report.component.html',
    styleUrls: ['./performance-report.component.css']
})
export class PerformanceReportComponent implements OnInit {
    //for datatable starts
    gettingDataFrmServiceFrSmryTable: any;


    truckPerfDataList: any;


    onDestroyUnsubscribtionSmry: Subscription;

    //for datatable ends

    //summaryTable:any;
    //for datepicker starts
    model: NgbDateStruct;
    model2;
    //for datepicker ends

    loadingIndicator = true;
    searchByHireslipNo = true;
    searchByDestination = false;
    viewDate = false;
    viewSource = true;
    viewDestination = false;
    searchByTruckNo = false;
    searchBySupplierName = false;

    selectSourceMain = true;
    selectSourceIndividual = false;

    //for datePicker starts
    hoveredDate: NgbDateStruct;
    fromDate: NgbDateStruct;
    toDate: NgbDateStruct;
    //for datepicker ends

    //for datatable starts
    @ViewChildren(DataTableDirective) public dtElements: QueryList<DataTableDirective>;
    dtTriggerTruckPerf: Subject<any> = new Subject();
    dataTable: any;
    dtOptionsTruckPerf: any;
    //for datatable ends

    isLoggedIn = true;
    userDataDtoReturnSession: any;
    address: any = null;
    showSpinnerForAction = false;

    agentDtoOptionSourceStationAll: AgentDetailsDto = new AgentDetailsDto();
    agentDtoOptionSourceStation: AgentDetailsDto[];
    agentDtoSourceStation: AgentDetailsDto = new AgentDetailsDto();
    public modelSourceStation: any;
    sourceStationTA: Array<AgentDetailsDto> = [];
    focusSourceStationTA$ = new Subject<string>();
    searchSourceStation = (text$: Observable<string>) => {
        const debouncedText$ = text$.pipe(debounceTime(200), distinctUntilChanged());
        const inputFocus$ = this.focusSourceStationTA$;

        return merge(debouncedText$, inputFocus$).pipe(
            map(term => (term === '' ? this.sourceStationTA
                : this.sourceStationTA.filter(v => v.mainBookStations.toLowerCase().indexOf(term.toLowerCase()) > -1)).slice(0, 200))
        );
    }
    formatterSourceStation = (x: { mainBookStations: string }) => x.mainBookStations;

    constructor(private performanceRpt: ReportService, private memoLessRpt: ReportService,
        private router: Router, private masterReadService: MasterReadService,
        private memoReport: MemoReport, private modalService: NgbModal) {
        if (sessionStorage.length == 0) {
            this.isLoggedIn = false;
            swal({
                title: "Session Expired",
                text: "Please relogin to access the application!",
                icon: "error",
                closeOnClickOutside: false,
                closeOnEsc: false,
            }).then(() => {
                this.logInPage();
            })
        }
        if (this.isLoggedIn) {
            this.userDataDtoReturnSession = JSON.parse(sessionStorage.getItem('SRDWeb'));
            this.address = this.userDataDtoReturnSession.addressId == null ? '' : this.userDataDtoReturnSession.addressId;
            this.getSourceDetails();
        }
    }

    logInPage() {
        this.router.navigate(['/authentication/login']);
    }

    ngOnInit(): void {

        this.dtOptionsTruckPerf = {
            dom: 'Bfrtip',
            buttons: [
                {
                    extend: 'excel',
                    text: '<i class="fas fa-file-excel"> Excel</i>',
                    titleAttr: 'Excel',
                    exportOptions: {
                        columns: ':visible'
                    }
                },
                {
                    extend: 'print',
                    text: '<i class="fas fa-print"> Print</i>',
                    titleAttr: 'Print',

                }
            ],
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            processing: true,
            responsive: true,
            "scrollX": true,
            "scrollY": 380,
            "scrollCollapse": true,
            "paging": true,
            "info": true,
        }
    }


    ngAfterViewInit(): void {
        this.dtTriggerTruckPerf.next();
    }


    rerender(): void {
        this.dtElements.forEach((dtElement: DataTableDirective) => {
            dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
                // Do your stuff
                dtInstance.destroy();
            });
        });
    }

    ngOnDestroy(): void {
        this.dtTriggerTruckPerf.unsubscribe();
        //this.onDestroyUnsubscribtionSmry.unsubscribe();
    }

    selectToday() {
        this.model = {
            year: my.getFullYear(),
            month: my.getMonth() + 1,
            day: my.getDate()
        };
    }

    searchByMode(searchBy: string) {
        if (searchBy === 'hireslipNumber') {
            this.searchByHireslipNo = true;
            this.searchByDestination = false;
            this.viewDate = false;
            this.viewSource = true;
            this.viewDestination = false;
            this.searchByTruckNo = false;
            this.searchBySupplierName = false;
            this.selectSourceMain = true;
            this.selectSourceIndividual = false;

        } else if (searchBy === 'destination') {
            this.searchByHireslipNo = false;
            this.searchByDestination = true;
            this.viewDate = true;
            this.viewSource = true;
            this.viewDestination = true;
            this.searchByTruckNo = false;
            this.searchBySupplierName = false;
            this.selectSourceMain = true;
            this.selectSourceIndividual = false;

        } else if (searchBy === 'truckNumber') {
            this.searchByHireslipNo = false;
            this.searchByDestination = false;
            this.viewDate = true;
            this.viewSource = false;
            this.viewDestination = false;
            this.searchByTruckNo = true;
            this.searchBySupplierName = false;
            this.selectSourceMain = false;
            this.selectSourceIndividual = false;

        } else if (searchBy === 'supplierName') {
            this.searchByHireslipNo = false;
            this.searchByDestination = false;
            this.viewDate = true;
            this.viewSource = false;
            this.viewDestination = true;
            this.searchByTruckNo = false;
            this.searchBySupplierName = true;
            this.selectSourceMain = false;
            this.selectSourceIndividual = false;

        } else {
            this.searchByHireslipNo = false;
            this.searchByDestination = false;
            this.viewDate = false;
            this.viewSource = false;
            this.viewDestination = false;
            this.searchByTruckNo = false;
            this.searchBySupplierName = false;
            this.selectSourceMain = false;
            this.selectSourceIndividual = false;
        }
    }


    selectSourceMode(selectSource: string) {
        if (selectSource === 'mainSource') {
            this.selectSourceMain = true;
            this.selectSourceIndividual = false;


        } else if (selectSource === 'individualSource') {
            this.selectSourceMain = false;
            this.selectSourceIndividual = true;


        } else {
            this.selectSourceMain = false;
            this.selectSourceIndividual = false;

        }
    }

    clickListnerForSourcStation(e: NgbTypeaheadSelectItemEvent, fubi: any) {
        this.modelSourceStation = e.item;
        $("#sourceStationId").val(this.modelSourceStation.mainBookStations)
    }

    getSourceDataDetails() {
        this.agentDtoSourceStation = new AgentDetailsDto();
        this.agentDtoSourceStation.companyId = this.userDataDtoReturnSession.companyId;
    }
    getSourceDetails() {
        this.getSourceDataDetails();
        this.showSpinnerForAction = true;
        this.masterReadService.getMainBookingStationsDetails(this.agentDtoSourceStation).subscribe(
            (response) => {
                console.log(response);
                if (response.length > 0) {
                    this.sourceStationTA = [];
                    this.agentDtoOptionSourceStation = [];
                    this.agentDtoOptionSourceStation = response;
                    this.agentDtoOptionSourceStationAll.mainBookStations = "All";
                    this.sourceStationTA.push(this.agentDtoOptionSourceStationAll);
                    for (let i = 0; i < this.agentDtoOptionSourceStation.length; i++) {
                        this.sourceStationTA.push(this.agentDtoOptionSourceStation[i]);
                    }
                    this.showSpinnerForAction = false;
                } else {
                    this.sourceStationTA = [];
                    this.agentDtoOptionSourceStation = [];
                    this.showSpinnerForAction = false;
                }
            }), (error) => {
                this.showSpinnerForAction = false;
                swal("Error", "Server Problem Occurred While getting the Stock At Details", "info");
            },
            () => console.log('done');

    };

}
